import { Box,StyledEngineProvider,styled } from '@mui/material'
import React , {useState,useEffect} from 'react'

import { useContext } from 'react'
import { DataContext } from '../../context/DataProvider'

const Container = styled(Box)`
    height: 30vh;
    margin: auto;
    width: 99%;
`


function Result() {

  const {html,css,js} = useContext(DataContext);
  const [src,setSrc] = useState('');

  const srcCode = `
    <html>
        <body>${html}</body>
        <style>${css}</style>
        <script>${js}</script>
    </html>
  `

  useEffect(() => {
    const timeOut = setTimeout(()=>{
        setSrc(srcCode)
    },1000)

    return () => {
        clearTimeout(timeOut);
    }
  },[html,css,js])

  return (
    <Container>
        <iframe
            srcDoc={src}
            title='Output'
            sandbox='allow-script'
            width='100%'
            height='100%'
            style={{border: 'none'}}
        />
    </Container>
  )
}

export default Result